@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Raleway:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #2B2B2B;
  display: flex;
  justify-content: center;
  font-family: 'Raleway', sans-serif;
}

.Mobile-container {
  width: 375px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #FFFFFF;
}

.App-link {
  color: #09d3ac;
}

.btn {
  border: none;
  border-radius: 25px;
  margin-top: 20px;
  width: 160px;
  height: 20px;
  padding-top: 13px;
  padding-bottom: 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-weight: 700;
  font-size: 12px;
  cursor: pointer;
  color: #ffffff;
  background-color: #53AEBA;
}

.btn:hover {
  background-color: #2B2B2B;
}
.StartPage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column wrap;
  font-size: 18px;
  color: #2B2B2B;
}

.StartPage-logo-wrap {
  /* border: 1px solid #53AEBA; */
  display: flex;
  justify-content: center;
  margin-top: 50%;
}

.StartPage-logo {
  width: 197px;
}

.StartPage-content {
  /* border: 1px solid #53AEBA; */
  text-align: center;
}

.StartPage-content p {
  margin-top: 0px;
  margin-bottom: 2px;
  font-size: 20px;
}

.StartPage-btn-wrap {
  /* border: 1px solid #53AEBA; */
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}
.QuestionsPage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column wrap;
  font-size: 18px;
  color: #2B2B2B;
  background-image: url(/olive-questions-app/static/media/question-bg.f22678fe.png);
  background-size: cover;
}

.Questions-num {
  /* border: 1px solid #53AEBA; */
  font-family: 'Abril Fatface', cursive;
  font-size: 40px;
}

.Questions-num p {
  margin: 0px 0px 0px 20px;
}

.Questions-content {
  /* border: 1px solid #53AEBA; */
  height: 65%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 28px;
}

.Questions-content p {
  /* border: 1px solid #53AEBA; */
  width: 85%;
  padding-top: 20px;
}

.Questions-btn-wrap {
  /* border: 1px solid #53AEBA; */
  height: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-btn {
  display: none;
}
.FinishPage {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column wrap;
  font-size: 18px;
  color: #2B2B2B;
  background-image: url(/olive-questions-app/static/media/finish-bg.039db90a.png);
  background-size: cover;
}

.FinishPage-content {
  /* border: 1px solid #53AEBA; */
  margin-top: 50%;
}

.FinishPage-content p {
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: 'Abril Fatface', cursive;
  font-size: 72px;
  text-align: center;
}

.FinishPage-btn-wrap {
  /* border: 1px solid #53AEBA; */
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}
